import { Button, Dialog, DialogContent, DialogTitle, List, ListItem, ListItemSecondaryAction, ListItemText, Modal, Paper, Radio, TextField, Typography } from "@material-ui/core";
import * as React from "react";
import { useMemo } from "react";
import { useFetcher } from "../../ajax";
import { useUserInfo } from "../../save";
import { useModalTheme } from "../../style";
import Defer from "../Defer";
import EditIcon from '@material-ui/icons/Edit';
import SwitchIcon from '@material-ui/icons/UnfoldMore';
import PlaceIcon from '@material-ui/icons/Place';
import { navigate } from "gatsby";

export const AlamatStyled = ({ alamat }) => {
    return <Typography style={{ marginLeft: '40px' }}>
        <b>{alamat.name}</b><br />
        {alamat.email} / {alamat.phone},<br />
        {alamat.complement}, {alamat.street},<br />
        {alamat.district}, {alamat.city}, <br />
        {alamat.region}, {alamat.state}, {alamat.postal}<br />
    </Typography>
}

const AddressPrompt = ({ open, value, list, onChange, onClose }) => {
    const modalTheme = useModalTheme();
    const user = useUserInfo();
    return (
        <Dialog open={open} onClose={onClose} fullWidth>
            <DialogTitle>Pilih Alamat</DialogTitle>
            <DialogContent>
                <Defer data={list}>
                    {() =>
                        <List>
                            {
                                list.map(x => <ListItem key={x.id} button onClick={() => onChange(x.id)}>
                                    <ListItemText primary={<>{x.label}{x.coordinate && <PlaceIcon fontSize="small" />}</>} secondary={
                                        <>{`${x.name}, ${x.district},`}<br />{` ${x.city}, ${x.region}, ${x.postal}`}</>
                                    } />
                                    <Radio checked={value === x.id} />
                                </ListItem>)
                            }
                            <ListItem>
                                <Button startIcon={<EditIcon />} onClick={() => window.open('http://app.dasawarsalink.id/user/alamat/', '_blank')} variant="contained">Ubah Data Alamat</Button>
                                <ListItemSecondaryAction>
                                    <Button onClick={onClose} variant="contained" color="primary">Tutup</Button>
                                </ListItemSecondaryAction>
                            </ListItem>
                        </List>}
                </Defer>
            </DialogContent>
        </Dialog>
    );
}

const AddressInput = ({ value, onChange }) => {
    const user = useUserInfo();
    const list = useFetcher(user && '/market/address/');
    const [open, setOpen] = React.useState(false);
    const keyedList = useMemo(() => list && list.data.reduce((a, b) => {
        a[b.id] = b;
        return a;
    }, {}), [list]);
    return <>
        <Button endIcon={<SwitchIcon />} onClick={() => setOpen(true)} variant="contained">
            {
                user && user.features.order ? (keyedList ?
                    <div>{value && keyedList[value] ? keyedList[value].label : 'Pilih Alamat'}</div>
                    : '...') : <div style={{ fontStyle: 'italic' }}>Tambahkan alamat</div>
            }
        </Button>
        <AddressPrompt open={open} value={value} list={list?.data} onChange={onChange} onClose={() => setOpen(false)} />
    </>
}

export default AddressInput
