import * as React from "react";
import { useUserInfo } from "../../components/save";
import Defer from "../../components/ui/Defer";
import ajax, { useFetcher } from '../../components/ajax';
import Navbar from "../../components/ui/Navbar";
import { Box, Breadcrumbs, Button, Checkbox, FormControl, List, ListItem, ListItemSecondaryAction, ListItemText, TextField, Typography, withStyles } from "@material-ui/core";
import { Link, navigate } from "gatsby";
import { expandImg, winalert } from "../../components/util";
import AddressInput from "../../components/ui/market/AddressInput";
import MediaInput from "../../components/ui/MediaInput";

/**
 * @type {import("@material-ui/core/styles/withStyles").Styles<any, any>}
 */
const styles = theme => ({
    head: {
        display: 'flex',
        alignItems: 'center',
    },
});

const Page = withStyles(styles)(({ classes }) => {
    const user = useUserInfo();
    const store = useFetcher(user && user.features.store && '/market/store/info');
    const [title, setTitle] = React.useState('');
    const [desc, setDesc] = React.useState('');
    const [logo, setLogo] = React.useState(null);

    React.useEffect(() => {
        if (store) {
            setTitle(store.data.title || '');
            setDesc(store.data.desc || '');
            setLogo(store.data.logo?.id);
        }
    }, [store]);

    const save = () => {
        ajax.post('/market/store/info/', {
            logo,
            desc,
            title
        }).then(() => {
            alert('Berhasil disimpan');
            navigate('/store/');
        }).catch(x => {
            alert(x.response?.data.message || x.message);
        });
    }
    return (
        <>
            <Navbar />
            <Defer data={user}>
                {() => (
                    <List>
                        <ListItem>
                            <Breadcrumbs>
                                <Link color="inherit" to="/store/">
                                    Atur Toko
                                </Link>
                                <Typography color="textPrimary">Edit Biodata</Typography>
                            </Breadcrumbs>
                        </ListItem>
                        <ListItem>
                            <FormControl fullWidth>
                                <TextField label="Nama Toko" variant="outlined" value={title} onChange={(e) => setTitle(e.target.value)} />
                            </FormControl>
                        </ListItem>
                        <ListItem>
                            <FormControl fullWidth>
                                <TextField multiline minRows={3} label="Deskripsi Toko" variant="outlined" value={desc} onChange={(e) => setDesc(e.target.value)} />
                            </FormControl>
                        </ListItem>
                        <ListItem>
                            <ListItemText primary="Logo" />
                            <Box flexGrow={1} />
                            <MediaInput value={logo} onChange={setLogo} />
                        </ListItem>
                        <ListItem>
                            <Box flexGrow={1} />
                            <Button onClick={save} disabled={!(title)} color="primary" variant="contained">
                                Simpan
                            </Button>
                        </ListItem>
                    </List>
                )}
            </Defer>
        </>
    );
});

export default Page;
